.homepage {
    display: block;
    width: auto;
    height: auto;
    // background-color: red;

    &-deneme {
        width: 100%;
        height: 500px;
        background-color: #ddd;
    }

    &-slider {
        display: block;
        width: 100%;
        height: 100%;
        max-height: 100vh;
        min-height: 100vh;
        position: relative;
        width: 100vw;
        min-width: 100vw;
        max-width: 100vw;
        resize: horizontal;
        left: 50%;
        right: 50%;
        margin-left: -50vw;
        margin-right: -50vw;
        filter: brightness(60%);
        resize: horizontal;
        overflow-y: auto;


        &-1, &-2, &-3, &-4 {
            display: inherit;
            width: 100%;
            height: 100vh;
            background-repeat: no-repeat;
        }
        &-1 {
            background-image: url("../../src/img/bg4.jpg");
            background-size: cover;
            background-position: center;
        }
        &-2 {
            background-image: url("../../src/img/bg3.jpg");
            background-size: cover;
            background-position: center;
            background-position-x: left;
            background-position-y: top;            
        }
        &-3 {
            background-image: url("../../src/img/bg6.jpg");
            background-size: cover;
            background-position: center;
        }
        &-4 {
            background-image: url("../../src/img/bg8.jpg");
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
        }
        
    }

    
}

