.carousel {
    &-cell {
        display: block;
        width: 100vw;
        height: 200px;
        background-color: red;
    }
}

.flickity-prev-next-button {
    width: 20px;
    height: 60px;
    border-radius: 5px;

    @include mq("tablet") {
        width: 30px;
        height: 80px;
        border-radius: 5px;
    }
}

.flickity-button {
    background: rgba($color: $color-dark-grey, $alpha: .5);


    &:hover {
        background: $color-dark-grey;
        transition: 0.2s;
    }

    &-icon {
        fill: rgba($color: $color-blue, $alpha: .5);
    }
}

.flickity-prev-next-button.previous {
    @include mq("tablet") {
        left: $offset;
    }
    left: 4px;
  }
  .flickity-prev-next-button.next {
    @include mq("tablet") {
        right: $offset;
    }
    right: 4px;
  }


.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 1s;
    z-index: 1000;
}

.preloader.complete {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

.preloader:before {
    content: '';
    position: absolute;
    left: 0;
    width: 50%;
    height: 100%;
    // background: $color-dark-grey;
    background: black;
    transition: 1s;
}

.preloader.complete:before {
    left: -50%;
}

.preloader:after {
    content: '';
    position: absolute;
    right: 0;
    width: 50%;
    height: 100%;
    // background: $color-dark-grey;
    background: black;
    transition: 1s;
}

.preloader.complete:after {
    right: -50%;
}

.loader {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50);
    width: 50px;
    height: 50px;
    box-sizing: border-box;
    // border: 3px solid black;
    border: 3px solid white;
    // background-color: white;
    animation: animate 2s linear infinite;
    // background-image: src("../../../src/img/");
    align-items: center;
    justify-content: center;
    z-index: 10000;
    img {
        // height: 80%;
        // width: 80%;
        width: 0%;
    }
}

.loader:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: white;
    // background: black;
    animation: animateBg 2s linear infinite;
}

@keyframes animate {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    25% {
        transform: translate(-50%, -50%) rotate(180deg);
    }
    50% {
        transform: translate(-50%, -50%) rotate(180deg);
    }
    75% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
    100% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

@keyframes animateBg {
    0% {
        height: 0;
    }
    25% {
        height: 0;
    }
    50% {
        height: 100%;
    }
    75% {
        height: 100%;
    }
    100% {
        height: 0;
    }
}